body,
html,
#root {
  height: 100vh;
}
html {
  font-size: 18px;
}
#Container {height: 100vh;}
h1 {
  padding: .77rem 0;
  margin: 0;
  background-color: #010c3f;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-size: 1.88rem;
  color: #fff;
}

h1 .highlight {
  color: #6c41ec;
}

h2 {
  font-family: 'Poppins', sans-serif;
  font-size: 1.55rem;
}

.App {
  height: 100vh;
  width: 100vw;
  animation: animateBg 30s linear infinite;
  background-image: linear-gradient(0deg,#6c41ec,#c19b4a,#def2a0,#c6ee4a,#42eca6,#64b3d9,#208ea2,#498ada,#5b73df,#897ed3,#6c41ec,#c19b4a);
  background-size: 100% 1100%;
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: #fff;
}

.App-playlist {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  height: 70vh;
}

@media only screen and (max-width: 1020px) {
  .App-playlist {
    align-items: center;
    flex-direction: column;
  }
}
@keyframes animateBg {
  0% { background-position: 0% 0%; }
  100% { background-position: 0% 100%; }
}