.SearchBar {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    padding-top: 6.94rem;
    margin-bottom: 6.33rem;
    height: 50px;
    justify-content: center;
  }
  
  .SearchBar input {
    width: 287px;
    padding: .88rem 0;
    border: 1px solid #fff;
    border-radius: 54px 0 0 54px;
    color: #010c3f;
    text-align: left;
    padding-left: 1rem;
    font-size: 1rem;
    width: 20vw;
  }
  
  .SearchBar input:focus {
    outline: none;
  }
  
  .SearchButton {
    cursor: pointer;
    width: 8.11rem;
    padding: .77rem 0;
    border-radius: 0 54px 54px 0;
    background-color: #010c3f;
    text-align: center;
    font-size: .833rem;
    transition: background-color .25s;
    border: 0px;
    color: #fff;
    font-weight: 500;
    padding: .88rem 0;
  }
  
  .SearchButton:hover {
    background-color: rgba(108, 65, 233, .7);
  }